.catalog-cards {
    padding-top: 60px;

}

.catalog-cards__list {
    display: grid;
    grid-template-columns: 300px 300px 300px;
    grid-template-rows: 123px 123px 123px;
    gap: 20px;       
    margin: 0;
    padding: 0;
    list-style: none;
    color: $white;   
}

.catalog-cards__item {
    padding-top: 20px;
    padding-left: 23px;
    background-repeat: no-repeat;
    background-position: 85% 50%;        
}

.catalog-cards__item:first-of-type {
    background-color: $yellow-card;
    background-image: url("../img/materials.png");    
}

.catalog-cards__item:nth-of-type(2) {
    background-color: $blue-card;
    background-image: url("../img/icon-drill.png");  
}

.catalog-cards__item:nth-of-type(3) {
    background-color: $pink-card;
    background-image: url("../img/icon-technic.png");  
}

.catalog-cards__item:nth-of-type(5) {
    background-color: $green-card;
    background-image: url("../img/icon-basket.png");
}

.catalog-cards__item:last-of-type {
    background-color: $yellow-card;
    background-image: url("../img/icon-logistic.png");     
}

.catalog-cards__title {
    margin: 0;
    margin-bottom: 15px;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
}

.catalog-cards__link {
    display: block;
    width: 135px;
    color: $white;
    background-color: $white-opacity;
    text-decoration: none;
    padding: 10px 5px;
    font-size: 14px;
    line-height: 18px;
    text-transform: uppercase;
    text-align: center;
}

.catalog-cards__item_slider {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 4;
    background-color: red;
    position: relative;
    padding: 0;  
}

.catalog-cards__slider-list {
    margin: 0;
    padding: 0;
    list-style: none;
    height: 100%;    
}

.catalog-cards__slider-item {
    height: inherit;
    padding-top: 23px;
    padding-left: 24px;    
}

.catalog-cards__slider-item_perforator {
    background: url("../img/perforator-bg.png");        
}

.catalog-cards__slider-item_drill {
    background: url("../img/drill-bg.png");   
}

.catalog-cards__slider-title {
    margin-top: 0;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 5px;
}

.catalog-cards__slider-arrow_left {
    position: absolute;
    top: 123px;
    left: 20px;
}

.catalog-cards__slider-arrow_right {
    position: absolute;
    top: 123px;
    right: 20px;    
}

.catalog-cards__catalog-open {
    position: absolute;
    color: $white;
    text-decoration: none;
    display: block;
    bottom: 27px;
    left: 23px;
    font-size: 14px;
    line-height: 18px;
    text-transform: uppercase;
    background-color: $basic-red;
    padding: 10px 46px;
}