.page-header {
    background: url("../img/top-background.png");    
    padding-bottom: 45px; 
}

.page-header__link {
    color: inherit;
    text-decoration: none;
}

.page-header__top-menu {
    background-color: $black-menu;
    color: $white;
    min-height: 30px;         
}

.page-header__top-menu-list {
    padding: 0;
    list-style: none;
    display: flex;
    position: relative;
    align-items: baseline;
    font-size: 18px;
    line-height: 24px;        
}

.page-header__top-menu-item {
    display: flex;
    align-items: center;
}

.page-header__top-menu-item_logo {
    position: absolute;     
}

.page-header__top-menu-item_search {
    margin-left: 270px;
}

.page-header__top-menu-item_bookmarks {
    margin-left: 193px;
}

.page-header__top-menu-item_basket {
    margin-left: 50px;
}

.page-header__top-menu-item_place-order-button {
    margin-left: 50px;
    background-color: $order-green;
    padding: 9px 19px;
}

.page-header__middle {
    display: flex;
    justify-content: space-between;
    padding-top: 40px;    
    align-items: baseline;
}

.page-header__title {    
    font-style: italic;
    font-size: 16px;
    line-height: 23px;
    color: $basic-red;
    width: 211px;
    margin: 0;
}

.page-header__contacts {
    width: 270px;
    flex-wrap: wrap;
}

.page-header__tel {
    display: block;
    color: $basic-red;
    text-decoration: none;
    font-weight: bold;
    font-size: 21px;
    line-height: 30px;
    padding: 7px 59px;
    border: 3px solid $white;
    box-sizing: border-box;
    margin-bottom: 10px;
}

.page-header__adress {
    display: block;
    font-size: 13px;
    line-height: 24px;
    text-decoration: none;
    color: $blue-basic;
    margin: 0 auto;
    width: 250px;
    text-align: center;
}

.page-header__middle-button {
    display: flex;
    color: $black;
    text-decoration: none;
    font-size: 21px;
    line-height: 21px;
    background-color: $white;  
    box-sizing: border-box;
    padding: 12px 24px;   
}

.main-nav {
    background-color: $blue-basic;
    margin-top: 39px;    
}

.main-nav__list {
    list-style: none;
    color: $white;
    font-size: 18px;
    line-height: 24px;
    display: flex;
    justify-content: space-around;
    padding: 18px 0;
}

.main-nav__link {
    color: $white;
    text-decoration: none;
}
